$red: rgb(235, 229, 209);

html {
    font-family:'Courier New', Courier, monospace;
    font-size: 16px;
}

body {
    height: 100vh;
    margin: 0;
    padding: 0;
    background: $red;
    * {
        // border: 1px solid rgb(205, 126, 57);
        margin: 0;
    }
}

h1 {
    font-size: 3rem;
}

img {
    display: block;
}

.container {
    display: flex;
    flex-flow: column;
    padding: 0rem 2rem;
    height: 100%;
}

.container-small {
    padding: 2rem 6rem;
}